function organizeKanaIntoRows(items) {
  const rows = [];
  let currentRow = [];

  items.forEach((item) => {
    currentRow.push(item);
    const rom = item.romanization;

    if (currentRow.length === 5) {
      rows.push([...currentRow]);
      currentRow = [];
    } else if (rom === 'yo' || rom === 'n') {
      rows.push([...currentRow]);
      currentRow = [];
    }
  });

  if (currentRow.length > 0) {
    rows.push(currentRow);
  }

  return rows;
}

